@media screen and (min-width: 320px) {
  .social-icons {
    display: flex;
    justify-content: center; }
  .social-icons > .icon-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 23.9%;
    height: 60px;
    color: white;
    background: orange; }
    .social-icons > .icon-wrap:hover {
      background: white;
      color: orange; }
  .social-icons > .icon-wrap > .icon {
    font-size: 32px; } }

@media screen and (min-width: 640px) {
  .social-icons > .icon-wrap {
    margin: 0 20px 0 20px; } }

@media screen and (min-width: 1000px) {
  .social-icons > .icon-wrap {
    margin: 0; } }

@media screen and (min-width: 1200px) {
  .social-icons {
    justify-content: space-between; }
  .social-icons > .icon-wrap {
    margin: 0; } }
