@media screen and (min-width: 320px) {
  .footer.ant-layout-footer {
    background: none;
    padding: 0;
    color: transparent;
    font-size: 14px; }
  .footer.ant-layout-footer > .container {
    display: flex;
    flex-direction: column;
    background-color: #000000;
    color: #ffffff;
    font-size: 17px;
    padding: 10px 25px 25px; }
  .footer.ant-layout-footer > .container > .description {
    z-index: 20; }
  .footer.ant-layout-footer > .container > .description > .text {
    text-align: center; }
  .footer.ant-layout-footer > .container > .description > .title {
    display: flex;
    justify-content: center;
    color: #ffa500;
    font-size: 25px; }
  .footer.ant-layout-footer > .container > .legal > .title {
    display: flex;
    justify-content: center;
    color: #ffa500;
    font-size: 25px; }
  .footer.ant-layout-footer > .container > .legal {
    display: flex;
    flex-direction: column;
    z-index: 20;
    line-height: 33px; }
  .footer.ant-layout-footer > .container > .legal > .link {
    display: flex;
    justify-content: center; }
  .footer.ant-layout-footer > .container > .legal > .link:hover > .title {
    color: #ffa500; }
  .footer.ant-layout-footer > .container > .legal > .link:hover > .title.-active {
    color: #ffffff; }
  .footer.ant-layout-footer > .container > .legal > .link > .title {
    color: #ffffff;
    font-size: 17px; }
  .footer.ant-layout-footer > .container > .legal > .link > .title.-active {
    color: #ffa500;
    font-size: 17px; } }

@media screen and (min-width: 640px) {
  .footer.ant-layout-footer > .container > .legal {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around; }
  .footer.ant-layout-footer > .container > .legal > .title {
    width: 100%; } }

@media screen and (min-width: 640px) {
  .footer.ant-layout-footer > .container > .legal {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center; }
  .footer.ant-layout-footer > .container > .legal > .link.-impressum {
    margin: 0 40px; } }

@media screen and (min-width: 1000px) {
  .footer.ant-layout-footer > .container {
    padding: 25px 40% 25px 70px;
    flex-direction: row; }
  .footer.ant-layout-footer > .container > .legal {
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-left: 75px; }
  .footer.ant-layout-footer > .container > .legal > .title {
    justify-content: flex-start; }
  .footer.ant-layout-footer > .container > .description > .title {
    justify-content: flex-start; }
  .footer.ant-layout-footer > .container > .legal > .link {
    justify-content: flex-start; }
    .footer.ant-layout-footer > .container > .legal > .link.-impressum {
      margin: 0; }
  .footer.ant-layout-footer > .container > .description > .text {
    text-align: left; } }
