@media screen and (min-width: 320px) {
  .post-wrap {
    display: flex;
    flex-direction: column;
    margin: 0 3%;
    padding: 40px 0; }
  .post-wrap > .title {
    display: flex;
    justify-content: center;
    font-family: 'Oswald', serif;
    font-size: 42px;
    font-style: normal;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.84);
    line-height: 1.04;
    letter-spacing: -.015em;
    text-transform: capitalize; }
  .post-wrap > .text {
    display: block;
    font-family: 'Roboto', serif;
    font-size: 21px;
    font-style: normal;
    font-weight: 400;
    color: rgba(0, 0, 0, 0.84);
    overflow-wrap: break-word;
    text-rendering: optimizelegibility;
    letter-spacing: -0.063px;
    line-height: 33.18px; }
  .post-wrap > .link {
    display: flex;
    align-items: center;
    align-self: flex-start;
    font-size: 18px;
    color: #ffa500; }
    .post-wrap > .link:hover {
      color: #000000; }
  .post-wrap > .link > .icon {
    margin-right: 10px; }
  .post-wrap > .link > .title {
    text-transform: lowercase; }
  .post-wrap .img {
    height: 25vh;
    background-position: 56% 44%;
    background-size: cover;
    background-origin: border-box;
    margin: 20px 0; }
    .post-wrap .img.-post {
      display: block;
      height: 25vh;
      min-height: 200px;
      width: auto;
      margin: 0 0 20px; } }

@media screen and (min-width: 900px) {
  .post-wrap {
    margin: 0 5%; }
  .post-wrap .img {
    height: 60vh;
    min-height: 500px; }
    .post-wrap .img.-post {
      display: inline-block;
      height: 40vh;
      min-height: 300px;
      width: 50%;
      margin: 0; }
      .post-wrap .img.-post.-left {
        float: left;
        margin-right: 20px; }
      .post-wrap .img.-post.-right {
        float: right;
        margin-left: 20px; } }
