@media screen and (min-width: 320px) {
  .blog-content {
    margin: 0 0 0; }
  .blog-content > .wrap > .item {
    padding-bottom: 24px;
    width: 100%; }
  .blog-content > .wrap > .item > .link > .img {
    width: 100%;
    height: 270px;
    background-size: cover;
    background-position: top center; }
    .blog-content > .wrap > .item > .link > .img:hover {
      opacity: .8; } }

@media screen and (min-width: 640px) {
  .blog-content > .wrap {
    display: flex;
    justify-content: space-between; }
  .blog-content > .wrap > .item {
    width: 49%; } }
