@media screen and (min-width: 320px) {
  .instagram-wrap {
    display: flex;
    flex-wrap: wrap;
    padding: 0 20px; }
  .instagram-wrap > .link {
    display: flex;
    margin-bottom: 20px;
    width: 100%; }
    .instagram-wrap > .link:hover {
      opacity: 0.8; }
  .instagram-wrap > .link > .image {
    width: 100%;
    height: 200px;
    background-size: cover; } }

@media screen and (min-width: 480px) {
  .instagram-wrap {
    display: flex; }
  .instagram-wrap > .link:first-of-type {
    margin-right: 2%; }
  .instagram-wrap > .link {
    width: 49%;
    margin-bottom: 0; } }

@media screen and (min-width: 900px) {
  .instagram-wrap > .link > .image {
    height: 250px;
    background-size: cover;
    background-position: center; } }

@media screen and (min-width: 1000px) {
  .instagram-wrap {
    padding: 0; }
  .instagram-wrap > .link:first-of-type {
    margin-right: 0; }
  .instagram-wrap > .link {
    width: 100%;
    margin-bottom: 20px; }
  .instagram-wrap > .link > .image {
    width: 100%;
    height: 200px;
    background-size: cover;
    background-position: center left; } }
